// start content
.tag {
  background-color: #ddd;
  border: none;
  color: black;
  padding: 2px 6px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  margin: 4px 2px;
  border-radius: 16px;
}

.avatar {
  border-radius: 50% !important;
}

.tag-kafka {
  background-color: #884400;
  color: white;
}

.tag-service-registry {
  background-color: #008844;
  color: white;
}

.tag-connectors {
  background-color: #440088;
  color: white;
}

.status-superseded {
  text-decoration: line-through;
}

.edit {
  float: right;
}

.post-toc {
  border: 1px solid black;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
}

div.post-toc h2 {
  font-size: 28px;
  margin-bottom: 0px;
}

.glossary dt {
  font-weight: bold;
}

:target {
  background-color: #ffa;
}
